.TodoItem-container{
  display: flex;
  flex-direction: column;
  align-items: center;
  align-content: center;
  justify-content: center;

}
.TodoItem {
    background-color: #94bce958;
    width: 55rem;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: -0.5em;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.15);
  }
  
  .TodoItem-p {
    margin: 24px 0 24px 24px;
    width: calc(100% - 120px);
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
  }
  .TodoItem-p--complete {
    text-decoration: line-through;
    color:rgba(115, 116, 116, 0.593)
    
  }
  
  .Icon {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 48px;
    width: 48px;
    font-size: 24px;
    font-weight: bold;
    /* background-color: #CCC; */
  }
  
  .Icon-check {
    position: absolute;
    left: 12px;
  }
  img{
    width: 1.3em;
  }
  
  .Icon-delete {
    position: absolute;
    top: -24px;
    right: 0;
  }
  .delete{
    width: 1.2em ;
    margin-top: 3em;
    margin-right: 1.1em;
  }
  
  